import React,{useEffect, useState} from 'react'
import StickyList from '../stickyList/StickyList'
import styles from './CreateSticky.module.css'

const CreateSticky = (props)=>{
    const [stickyNote,setStickyNote] = useState('')
    const [error,setError] = useState('')
    const {setStickyList,stickyList} = props
    
    

    const clearList = ()=>{
        localStorage.removeItem('stickylist')
        setStickyList([])
    }
    

    const handleSubmit = (e)=>{
        e.preventDefault()

      

        if(stickyNote.length>0){
            setError('')
            setStickyList([...stickyList,stickyNote])
        }else{
            setError('Debes escribir algo para recordarte!')
        }

        setStickyNote('')
    }

    return(
        <>
        <div className={styles.contenedorPadre}>
        <p><b>Ingresa lo que quieras recordar!</b></p>

        <div className={styles.formContainer}>
            <button className={styles.clearList} disabled={stickyList.length>0?false:true} onClick={clearList}>Vaciar lista de tareas</button>
            <input value={stickyNote} onChange={(e) => {setStickyNote(e.target.value)}} className={styles.createInput} type="text"/>
            <input type='submit' className={styles.submitButton} onClick={handleSubmit} value="Guardar recordatorio"/>
        </div>
    {error?<span>{error}</span>:''}
    </div>
        </>
    )
}

export default CreateSticky