import React,{useEffect} from 'react'
import StickyNote from './stickyNote.jsx'
import styles from './stickylist.module.css'

const StickyList = (props)=>{
    let {stickyList,setStickyList} = props


    const deleteNote = (id)=>{
        setStickyList(stickyList.filter((item,i)=>{
            return i !== id
        }))

        
    }
   


    useEffect(()=>{
        localStorage.setItem('stickylist',stickyList)
    },[stickyList])

    return(
        <>
        <div className={styles.stickyListContainer2}>
        </div>
        <div className={styles.stickyListContainer}>
            {stickyList.length>0?stickyList.map((note,i)=>{
                return <StickyNote onDelete={deleteNote} id={i} key={i} stickyNote={note}/>
            }):'No tenes ninguna nota'}
        </div>
        </>
    )
}

export default StickyList